    -webkit-animation: move 1s linear infinite forwards;
            animation: move 1s linear infinite forwards;
}

/* ============= Genreal Styles ============ */

/* Making the container width 75% and making it centered. */
.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

/* Making the font weight of the h1, h2, h3, h4, and h5 tags 500. */
h1, 
h2, 
h3, 
h4, 
h5 {
    font-weight: 500;
}

/* Making the font size of the h1 tag 2.5rem. */
h1{
    font-size: 2.5rem;
}

/* Making the margin top of the section tag 8rem. */
section {
    margin-top: 8rem;
}

/* Making the text align center and the color light. */
section > h2, 
section > h5 {
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

/* Making the color of the h2 tag primary and the margin bottom 3rem. */
section > h2{
    color: #4db5ff;
    color: var(--color-primary);
    margin-bottom: 3rem;
} 

/* Making the color of the text light. */
.text-light {
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

/* Making the color of the a tag primary and the transition 400ms ease. */
a {
    color: #4db5ff;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

/* Making the color of the a tag white when the mouse is hovering over it. */
a:hover {
    color: #fff;
    color: var(--color-white);
}

/* The above code is a CSS code that is used to style the HTML code. */
.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #4db5ff;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover {
    background: #fff;
    background: var(--color-white);
    color: #1f1f38;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: #4db5ff;
    background: var(--color-primary);
    color: #1f1f38;
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

 /* ============ MEDIA QUERIES (MEDIUM DEVICES) ============ */
 @media screen and (max-width: 1024px) {
    .container {
        width: 86%;
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
 }

  /* ============ MEDIA QUERIES (SMALL DEVICES) ============ */
  @media screen and (max-width: 600px) {
    .container{
